import React from "react";
import Header from "../header/header";
import { Box, IconButton, Typography, Button } from "@mui/material";
import { defaultColor } from "../../constant/color_constant";
import ArrowRightOutlinedIcon from "@mui/icons-material/ArrowRightOutlined";
import { useNavigate } from "react-router-dom";
import TextField from "@mui/material/TextField";
import { useState } from "react";
import { useLocation } from 'react-router-dom';
import { useTheme } from '@mui/material/styles';

export default function SignupConfirmationPage() {  
    return (
        <>
            <Box
                sx={{
                    display: "flex",
                    flexDirection: "column",
                    height: "100%",
                }}
            >
                <Header />
                {/* 89.4% */}
                <SignupConfirmationTitle></SignupConfirmationTitle>

                <SignupConfirmationContent></SignupConfirmationContent>
            </Box>
        </>
    );
}

function SignupConfirmationTitle() {
    return (
        <Box
            sx={{
                height: "10%",
                bgcolor: "orange",
                borderTop: "1px solid black",
                borderBottom: "1px solid black",
                padding: "16px",
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
            }}
        >
            <Typography sx={{ fontSize: "20px" }}></Typography>
        </Box>
    );
}

function SignupConfirmationContent() {
    const navigate = useNavigate();
    const location = useLocation();
    const { email, name, nameKana, userID, password } = location.state;

    const [showPassword, setShowPassword] = useState(false);

    const handleShowPassword = () => {
        setShowPassword(!showPassword);
    };
    
    const theme = useTheme();
    
    const typographyStyle = {
        marginBottom: 2,
    };

    return (
        <Box 
            sx={{ 
                height: "100%", 
                bgcolor: defaultColor,
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'space-around',
                alignItems: 'center',
            }}
        >
            <Box 
                sx={{ 
                    width: 'max-content', 
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'flex-start',
                }}
            >
                <Typography sx={typographyStyle}>: {name}</Typography>
                <Typography sx={typographyStyle}> (): {nameKana}</Typography>
                <Typography sx={typographyStyle}>: {email}</Typography>
                <Typography sx={typographyStyle}>ID: {userID}</Typography>
                <Typography sx={typographyStyle}>: {showPassword ? password : '*'.repeat(password.length)}</Typography>
                <Button onClick={handleShowPassword} sx={{ marginTop: 2 }}>{showPassword ? 'Hide Password' : 'Show Password'}</Button>
            </Box>
            <Box
                sx={{
                    textAlign: "center",
                    height: "17%",
                    paddingTop: "30px",
                }}
            >
                <Button
                    type="button"
                    onClick={() =>
                        // welcome.jsx
                        navigate("../")
                    }
                    sx={{
                        textAlign: "center",
                        height: "100%",
                        width: "150%",
                        bgcolor: "orange",
                        color: "white",
                    }}
                >
                    
                </Button>
            </Box>
        </Box>
    );
}